::placeholder {
	color: #999; }

.header-search-form {
	position: absolute;
	top: 41px;
	margin-left: 790px;
	@include xxl {
		left: 80px; }
	@include xl {
		left: -460px; } }
.header-search-form input[name="query"] {
	padding-left: 22px;
	font-size: 18px;
	right: 1080px;
	background: #EFEFEF;
	width: 463px;
	height: 54px;
	border-radius: 30px;
	border: none;
	&:focus {
		outline: none; }
	&:disabled {
		background: #fff; }
	::placeholder {
		color: #999; }
	@include xxl {
		width: 300px; }
	@include xl {
		width: 463px; } }
.header-search-form button[type="submit"] {
	position: absolute;
	padding: 17px 0;
	cursor: pointer;
	margin-left: -49px;
	background: none;
	border-width: initial;
	border-style: none;
	border-color: initial;
	border-image: initial;
	&:focus {
		outline: none; } }

