.menu {
	padding: 31px;
	position: relative;
	height: 83px;
	background: #3E3E3E;
	max-width: 1920px;
	margin: 0 auto;
	@include xxl {
		width: 100%; }
	@include xl {
		width: 100%; }
	@include lg {
		width: 100%; }
	//+md
	///width: 100%
	.list {
		display: flex;
		align-items: center;
		margin-left: 140px;

		@include lg {
			//position: fixed
			position: absolute;
			flex-direction: column;
			justify-content: center;
			margin: 0;
			line-height: 70px;
			top: 0;
			//right: 0
			left: -100%;
			width: 50%;
			height: 100vh;
			padding: 20px 15px;
			z-index: 1;
			background-color: rgba(0,0,0, .8);
			transition: 0.6s all;
			&_item {}
			@include lg {
				padding-right: 0;
				border: none; }
			@include md {
				padding: 0;
				height: 450px; }
			&_active {
				left: 0; } } }
	.hamburger {
		display: none;
		@include lg {
			display: block;
			position: absolute;
			right: 20px;
			top: 50%;
			transform: translateY(-50%);
			width: 30px;
			height: 20px;
			span {
				display: block;
				height: 4px;
				width: 100%;
				background-color: #fff;
				margin-bottom: 4px;
				transform: 0.5s all; }
			&_active {
				span {
					margin-bottom: -4px;
					&:nth-child(1) {
						transform: translateY(3px) rotate(-45deg); }
					&:nth-child(2) {
						display: none; }
					&:nth-child(3) {
						transform: translateY(3px) rotate(45deg); } } } } }

	&__item {
		margin-left: 125px;
		@include xxl {
