// Fonts
$deff: 'Poppins', sans-serif;
$second_font: "Roboto", sans-serif;


// Colors
$c0: #000;
$c1: #FF8080;
$c2: #E5E5E5;
$c3: #696d6b;
// $c2: #
// $c3: #


// Hamburger
$hamburger-padding-x          	: 0;
$hamburger-padding-y          	: 0;
$hamburger-layer-width        	: 17px;
$hamburger-layer-height       	: 1px;
$hamburger-layer-spacing      	: 5px;
$hamburger-layer-color        	: #000;
$hamburger-layer-border-radius	: 0;
$hamburger-hover-opacity      	: 1;


// Media queries breakpoints for mixins
$xsm: 375px;

// Small 576px
$sm: 576px;
$sm_maxw: 768px;

// Medium 768px
$md: 768px;
$md_maxw: 992px;

// Large 992px
$lg: 992px;
$lg_maxw: 1200px;

// Extra Large 1200px
$xl: 1200px;
$xl_maxw: 1240px;

// Extra Large 1440px



// Extra Large 1440px
$xxl: 1820px;
$xxl_maxw: 1720px;

// Extra Large 1440px
$xxxl: 1600px;


// Vertical breakpoints
$h800: 800px;

//portfolio
$h730: 730px;


// ==========================================================================
// Bootstrap
// ==========================================================================
$grid-gutter-width: 30px;
// Media queries breakpoints
$grid-breakpoints: (xs: 0, sm: $sm, md: $md, lg: $lg, xl: $xl, xxl: $xxl);
// grid container width
$container-max-widths: (sm: $sm_maxw, md: $md_maxw, lg: $lg_maxw, xl: $xl_maxw, xxl: $xxl_maxw);




